                  p.color-adicional-4 Según Freeman Tilden (2008), la interpretación debe conectar la información con las emociones y las experiencias previas del visitante, para que el mensaje se transforme en un aprendizaje significativo. En este sentido, el guía turístico debe poseer un amplio conocimiento del territorio en sus distintas dimensiones: riqueza natural, dinámicas sociales, valores culturales, hitos históricos y expresiones simbólicas.

